import CanvasNest from 'canvas-nest.js';
import DragVerify from '@/components/common/DragVerify.vue'; //  引入滑动解锁组件

import { login } from '@/request/user.js'; // 配置

var config = {
  color: '64, 158, 255',
  // 线条颜色
  pointColor: '64, 158, 255',
  // 节点颜色
  opacity: 0.5,
  // 线条透明度
  count: 99,
  // 线条数量
  zIndex: -1 // 画面层级

};
export default {
  name: 'Login',
  components: {
    DragVerify: DragVerify
  },
  data: function data() {
    return {
      // 登录表单数据
      loginForm: {
        telephone: '',
        password: ''
      },
      // 登录表单验证规则
      loginFormRules: {
        telephone: [{
          required: true,
          message: '请输入手机号',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '请输入密码',
          trigger: 'blur'
        }, {
          min: 5,
          max: 20,
          message: '长度在 5 到 20 个字符',
          trigger: 'blur'
        }]
      },
      isPassing: false,
      //  滑动解锁是否验证通过
      loginBtnDisabled: true //  登录按钮是否禁用

    };
  },
  computed: {
    url: function url() {
      var _url = this.$route.query.Rurl; //  获取路由前置守卫中 next 函数的参数，即登录后要去的页面

      return _url ? {
        path: _url
      } : {
        name: 'Stock'
      }; //  若登录之前有页面，则登录后仍然进入该页面
    }
  },
  watch: {
    //  滑动解锁验证通过时，若重新输入用户名或密码，滑动解锁恢复原样
    'loginForm.telephone': function loginFormTelephone() {
      this.isPassing = false;
      this.$refs.dragVerifyRef.reset();
    },
    'loginForm.password': function loginFormPassword() {
      this.isPassing = false;
      this.$refs.dragVerifyRef.reset();
    }
  },
  created: function created() {
    // 用户若已登录，自动跳转到首页
    if (this.$store.getters.isLogin) {
      var username = this.$store.getters.username;
      this.$message({
        message: "".concat(username, " \u60A8\u5DF2\u767B\u5F55\uFF01\u5DF2\u8DF3\u8F6C\u5230\u9996\u9875"),
        center: true,
        type: 'success'
      });
      this.$router.replace({
        name: 'Home'
      });
    } //  绘制背景图


    this.$nextTick(function () {
      var element = document.getElementById('loginBackground');
      new CanvasNest(element, config);
    });
  },
  methods: {
    /**
     * 滑动解锁完成 回调函数
     * @param {boolean} isPassing 解锁是否通过
     */
    updateIsPassing: function updateIsPassing(isPassing) {
      if (isPassing) {
        this.loginBtnDisabled = false;
      } else {
        this.loginBtnDisabled = true;
      }
    },

    /**
     * 登录按钮点击事件 表单验证&用户登录
     * @param {boolean} formName 表单ref值
     */
    submitForm: function submitForm(formName) {
      var _this = this;

      this.$refs[formName].validate(function (valid) {
        if (valid) {
          // 表单各项校验通过
          login(_this.loginForm, true).then(function (res) {
            if (res.success) {
              _this.setCookies('token', res.data.token); //  存储登录状态


              _this.$refs[formName].resetFields(); //  清空表单


              _this.$router.replace(_this.url); //  跳转到前一个页面或者网盘主页

            } else {
              _this.$message.error('手机号或密码错误！');

              _this.isPassing = false;

              _this.$refs.dragVerifyRef.reset();
            }
          });
        } else {
          _this.$message.error('请完善信息！');

          return false;
        }
      });
    }
  }
};